.tooltip.is-secondary:after {
  background: #ff7f2a;
  color: #fff;
}

.tooltip:not([data-label=""]).is-always:before,
.tooltip:not([data-label=""]).is-always:after {
  opacity: 1;
  visibility: visible;
}

.tooltip.is-multiline:after {
  display: flex-block;
  text-align: center;
  white-space: normal;
}

.tooltip.is-dashed {
  border-bottom: 1px dashed #b5b5b5;
  cursor: default;
}

.tooltip.is-square:after {
