.sidebar {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  max-width: calc( 100% - 50px);
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 19;
  transform: translateZ(0);
  transition: .3s ease;
  backface-visibility: hidden;
  perspective: 1000;
  will-change: transform;
}

@media screen and (max-width: 768px) {
  .sidebar {
    width: 100%;
    right: -100%;
  }
}

@media screen and (min-width: 769px), print {
  .sidebar {
    width: 50%;
    right: -50%;
  }
}

@media screen and (min-width: 1024px) {
  .sidebar {
    width: 35%;
    right: -35%;
  }
}

@media screen and (min-width: 1216px) {
  .sidebar {
    width: 30%;
    right: -30%;
  }
}

@media screen and (min-width: 1408px) {
  .sidebar {
    width: 25%;
    right: -25%;
  }
}

@media screen and (max-width: 768px) {
  .sidebar.is-left {
    left: -100%;
  }
}

@media screen and (min-width: 769px), print {
  .sidebar.is-left {
    left: -50%;
  }
}

@media screen and (min-width: 1024px) {
  .sidebar.is-left {
    left: -35%;
  }
}

@media screen and (min-width: 1216px) {
  .sidebar.is-left {
    left: -30%;
  }
}

@media screen and (min-width: 1408px) {
  .sidebar.is-left {
    left: -25%;
  }
}

.sidebar.is-active {
